$(document).ready(function () {

    /*sticky top*/
    var sticky = $('header');

    $(window).scroll(function(){
        if ( $(this).scrollTop() > 1280 && sticky.hasClass('default') ){
            sticky.removeClass('default').addClass('fixed');
        } else if($(this).scrollTop() <= 1280 && sticky.hasClass('fixed')) {
            sticky.removeClass('fixed').addClass('default');
        }

        /* animated keynumbers */
        $('.section__keynumbers').waypoint(function() {
            $('.js-chart').each(function() {
                CharsStart();
            });
        }, {
            offset: '80%'
        });

    });

    /* pie chart */
    function CharsStart() {

        $('.js-chart').easyPieChart({
            barColor: false,
            trackColor: false,
            scaleColor: false,
            scaleLength: false,
            lineCap: false,
            lineWidth: false,
            size: false,
            animate: 5000,

            onStep: function(from, to, percent) {
                $(this.el).find('.js-percent').text(Math.round(percent));
            }
        });
    }

    /* anchor smooth scroll */
    $(".mouse_scroll").on('click', function (event) {
        event.preventDefault();
        var id  = $(this).attr('href'),
            top = $(id).offset().top - 180;
        $('body,html').animate({scrollTop: top}, 1500);
    });

    /* Back to top btn */
    $(function(f) {
        var element = f('.to-top__holder');
        f(window).scroll(function(){
            element['fade'+ (f(this).scrollTop() > 1650 ? 'In': 'Out')](500);
        });
    });
    $('.to-top__holder').on('click', '.back-to-top', function (e) {
        e.preventDefault();
        $('html, body').animate({scrollTop: 0}, 1500);
    });

    /* popup window */
    $('#popup').popup({
        transition: 'all 0.3s',
        scrolllock: true
    });

    /* animated Entrances */
    var AnimatedEntrances = true; // Set false for turn off animation

    if (AnimatedEntrances) {
        var wow = new WOW(
            {
                boxClass:     'wow',      // animated element css class (default is wow)
                animateClass: 'animated', // animation css class (default is animated)
                offset:       0,          // distance to the element when triggering the animation (default is 0)
                mobile:       true,       // trigger animations on mobile devices (default is true)
                live:         true,       // act on asynchronously loaded content (default is true)
                callback:     function(box) {
                    // the callback is fired every time an animation is started
                    // the argument that is passed in is the DOM node being animated
                },
                scrollContainer: null // optional scroll container selector, otherwise use window
            }
        );
        wow.init();
    }

    $('.form-popup').submit(function (e) {
        var $form = $(e.target);
        e.preventDefault();
        $('.btn[type="submit"]').prop("disabled", true);

        $.ajax({
            url: $form.attr('action'),
            type: "POST",
            data: $form.serialize(),
            dataType: 'json',
            success: function (data) {
                toastr.success('<strong>Success!</strong> Check your email.');
                $('.btn[type="submit"]').prop("disabled", false);
                $('#popup').popup('hide');
                return true;
            },
            error: function (data) {
                var result = JSON.parse(data.responseText).errors;
                var string = '';

                for (obj in result) {
                    string += result[obj][0];
                    string += "\n";
                }
                var escaped = $('<div>').text(string).text();
                toastr.error(escaped.replace(/\n/g, '<br /><br />'));

                $('.btn[type="submit"]').prop("disabled", false);

            }
        });
    })

});